import React from 'react'
/* this area is responsable for the functionalities of the search bar on the left side under the header*/
const Filter = () => {
    window.addEventListener('DOMContentLoaded', () => { 
    const search = document.getElementById('search')

    search.addEventListener('click' , (e) => {
        const {value} = e.target

        const countryName = document.querySelectorAll('.country-name')
        
        countryName.forEach((name) => {
            if (name.innerText.toLowerCase().includes(value.toLowerCase())) {
              name.parentElement.parentElement.style.display = 'block'  
            } else {
                name.parentElement.parentElement.style.display = 'none'
            }
        })
       }) 
    })

/*this area is reponsable for the option bar on the right side, giving user the option to group 
countries by continents*/
    return (
        <section className="filter">
           <form className="form-control">
               <input type="search" name="search" id="search" placeholder="Search for a counrty"/>
               </form> 

            <div className='region-filter'>
                <select name="select" id="select" className="select">
                  <option value="Filter by region">Filter by region</option>  
                  <option value="Africa">Africa</option>  
                  <option value="America">America</option>  
                  <option value="Europe">Europe</option>  
                  <option value="Asia">Asia</option>  
                  <option value="Oceania">Oceanina</option>  
                </select>
            </div>

        </section>
    )
}


export default Filter
